.modal-project-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.75);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  overflow: auto;
  padding-top: 5rem;
}

.modal-project-wrapper.active {
  z-index: 10;
  opacity: 1;
  position: fixed;
}

.modal-content-container {
  margin-top: 6rem  ;
  margin-bottom: 3rem ;
  max-width: 550px;
  width: 100%;
  background-color: rgb(23, 23, 33);
  color: rgb(242, 243, 244);
  padding: 20px;
  border-radius: 16px;
  overflow: auto;
}

.modal-content {
  position: relative;
}

.modal-project {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  margin: 3rem 0;
}

.close-button {
  display: flex;
  justify-content: flex-end;
  cursor: pointer;
  padding-right: 5px;
}

.modal-image {
  width: 100%;
  max-height: 400px;
  object-fit:contain;
  border-radius: 12px;
  margin-top: 20px;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 10px 0px;
}

.modal-title {
  font-size: 28px;
  font-weight: 600;
  color: rgb(242, 243, 244);
  margin: 8px 6px 0px;
}

.modal-tags {
  display: flex;
  flex-wrap: wrap;
  margin: 8px 0px;
}

.tag {
  font-size: 14px;
  font-weight: 400;
  color: rgb(133, 76, 230);
  margin: 4px;
  padding: 4px 8px;
  border-radius: 8px;
  background-color: rgba(133, 76, 230, 0.125);
}

.modal-description {
  font-size: 16px;
  font-weight: 400;
  color: rgb(242, 243, 244);
  margin: 8px 6px;
}